import React from "react";
import { ProgressBar } from "react-step-progress-bar";
import "react-step-progress-bar/styles.css";

class StepProgressBar extends React.Component {
  render() {
    return (
      //move it closer to the cards
      <div
        style={{
          marginTop: "100px",
          marginLeft: "-28px",
          marginRight: "-28px"
        }}
      >
        <ProgressBar
          percent={!this.props.percent ? 0 : this.props.percent}
          filledBackground="linear-gradient(to right, #dc3745, #d9717a)"
        ></ProgressBar>
      </div>
    );
  }
}

export default StepProgressBar;
