import React from "react";
import { Button, PopoverBody, UncontrolledPopover } from "reactstrap";

class NumericalButtons extends React.Component {
  constructor(props) {
    super(props);
    this.handlePlot = this.handlePlot.bind(this);
    this.state = {};
  }
  handlePlot() {
    this.setState({ clicked: true });
    this.props.onPlot(
      "final",
      "Create line plot from Entities JSON (Visualisation)",
      this.props.slot
    );
  }
  render() {
    return (
      <div>
        <Button
          id="plotButton"
          block
          size="sm"
          color="danger"
          onClick={this.handlePlot}
        >
          Plot counts
        </Button>
        <UncontrolledPopover
          trigger="hover"
          placement="right"
          target="plotButton"
        >
          <PopoverBody>
            Plot the counts.{" "}
            <a href="/tutorial" target="_blank" rel="noopener noreferrer">
              More information
            </a>
          </PopoverBody>
        </UncontrolledPopover>
      </div>
    );
  }
}

export default NumericalButtons;
