import React from "react";
import Chart from "react-google-charts";
import {
  Card,
  CardBody,
  CardTitle,
  Col,
  Row,
  Input,
  Form,
  Label,
} from "reactstrap";
import { DebounceInput } from "react-debounce-input";

class WordTreeRow extends React.Component {
  render() {
    if (!this.props.datasets) return null;
    return (
      <Row>
        {this.props.datasets.map(
          (dataset) =>
            dataset.tree && (
              <WordTree
                title={this.props.title}
                key={dataset.name}
                phrases={dataset.tree}
                focusWord={dataset.term}
              />
            )
        )}
      </Row>
    );
  }
}

class WordTree extends React.Component {
  constructor(props) {
    super(props);
    this.handleChange = this.handleChange.bind(this);
    this.state = {
      type: "double",
      data: null,
      focusWord: null,
    };
  }
  componentDidMount() {
    this.setState({
      data: this.props.phrases,
      focusWord: this.props.focusWord,
    });
  }

  handleChange({ target }) {
    console.log(target.name, target.value);
    this.setState({
      [target.name]: target.value,
    });
  }

  render() {
    return (
      <Col sm="6">
        <Card className="shadow justify-content-between">
          <CardBody>
            <CardTitle className="text-center">
              <h3>Word Tree</h3>
            </CardTitle>
            <Form inline>
              <Label for="wordInput" className="mr-2">
                Search:
              </Label>
              <DebounceInput
                id="wordInput"
                name="focusWord"
                className="form-control mr-2"
                value={this.state.focusWord}
                minLength={2}
                debounceTimeout={900}
                onChange={(evt) => this.handleChange(evt)}
              />
              <Input
                value={this.state.type}
                type="select"
                name="type"
                id="typeInput"
                onChange={this.handleChange}
              >
                <option value="suffix">Word before</option>
                <option value="prefix">Word after</option>
                <option value="double">Word middle</option>
              </Input>
            </Form>
            <Chart
              loader={<div>Loading Chart</div>}
              chartType="WordTree"
              data={this.props.phrases}
              options={{
                wordtree: {
                  format: "implicit",
                  word: this.state.focusWord,
                  type: this.state.type,
                },
              }}
              rootProps={{ "data-testid": "1" }}
            />
          </CardBody>
        </Card>
      </Col>
    );
  }
}

export default WordTreeRow;
