import React from "react";
import MyNavbar from "./navbar.js";
import Pipeline from "./pipeline.js";
import Dashboard from "./dashboard.js";
import Home from "./home.js";
import About from "./about.js";
import Tutorial from "./tutorial.js";
import { Container, Row, Col } from "reactstrap";
import getURL, {
  BASE_URL,
  HEADERS,
  createTableName,
  countArticles,
  countWords,
} from "./utilities.js";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      steps: [],
      prettySteps: [],
      datasets: [],
      loading: false,
    };
    this.updateState = this.updateState.bind(this);
    this.removeAllKeysExcept = this.removeAllKeysExcept.bind(this);
    this.handleError = this.handleError.bind(this);
    this.examplePipeline = this.examplePipeline.bind(this);
    this.handleStatementText = this.handleStatementText.bind(this);
    this.handleStatementFrame = this.handleStatementFrame.bind(this);
    this.handleFinal = this.handleFinal.bind(this);
    this.handleAdd = this.handleAdd.bind(this);
    this.handlePlot = this.handlePlot.bind(this);
    this.handleWordCounts = this.handleWordCounts.bind(this);
    this.handleArticleCounts = this.handleArticleCounts.bind(this);
    this.handleFetch = this.handleFetch.bind(this);
    this.handleDelete = this.handleDelete.bind(this);
    this.handlePlotCauses = this.handlePlotCauses.bind(this);
    this.handleDistribution = this.handleDistribution.bind(this);
    this.handlePlotCausalMap = this.handlePlotCausalMap.bind(this);
  }

  removeAllKeysExcept(json, keys) {
    keys.push("data", "term", "name", "number", "start", "stop");
    let result = {};
    Object.keys(json).forEach((key) => {
      if (keys.includes(key)) result[key] = json[key];
    });
    return result;
  }

  updateState(step, name, slot, keysToKeep, keyToSet, optionalFunc) {
    this.setState((prevState) => ({
      steps: [...prevState.steps.splice(0, slot - 1), step],
      prettySteps: [...prevState.prettySteps.splice(0, slot - 1), name],
      datasets: [
        ...prevState.datasets.map((dataset) => {
          dataset = this.removeAllKeysExcept(dataset, keysToKeep);
          dataset[keyToSet] = true;
          if (optionalFunc) dataset = optionalFunc(dataset);
          return dataset;
        }),
      ],
      loading: false,
    }));
  }

  handleError(error) {
    alert("Server error, please try again later.");
    this.setState((prevState) => ({
      datasets: prevState.datasets.map((d) => this.removeAllKeysExcept(d, [])),
      loading: false,
    }));
  }

  handleAdd(source, term, start, stop, number, comments, subreddit) {
    this.setState({ steps: [], loading: true });
    const name = createTableName(
      source,
      comments,
      subreddit,
      term,
      start,
      stop,
      number
    );

    if (this.state.datasets.some((e) => e.name === name)) return;

    let url = getURL(source, term, start, stop, number, comments, subreddit);
    fetch(url, {
      method: "GET",
    })
      .then((data) => data.json())
      .then((data) => {
        if (data.length === 0) return;
        this.setState((previousState) => ({
          datasets: [
            ...previousState.datasets.map((d) =>
              this.removeAllKeysExcept(d, [])
            ),
            {
              name: createTableName(
                source,
                comments,
                subreddit,
                term,
                start,
                stop,
                data.length
              ),
              term: term,
              start: start,
              stop: stop,
              number: data.length,
              data: data,
            },
          ],
          loading: false,
        }));
      })
      .catch((e) => {
        this.handleError(e);
      });
  }

  handleFinal(step, name, slot) {
    this.setState((prevState) => ({
      steps: [...prevState.steps.splice(0, slot - 1), step],
      prettySteps: [...prevState.prettySteps.splice(0, slot - 1), name],
    }));
  }

  handleDelete(toDelete) {
    const keep = this.state.datasets.filter((set) => set.name !== toDelete);
    const result = keep.map((d) => this.removeAllKeysExcept(d, []));
    this.setState({ steps: [], datasets: result });
  }

  handlePlot(step, name, slot) {
    "Plot the Entities counts over time";
    this.updateState(
      step,
      name,
      slot,
      ["entities", "articleCounts", "wordCounts"],
      ["showEntitiesPlot"]
    );
  }

  handleArticleCounts(step, name, slot) {
    this.updateState(step, name, slot, [], [], countArticles);
  }

  handleWordCounts(step, name, slot) {
    this.updateState(step, name, slot, [], [], countWords);
  }

  handleDistribution(step, name, slot) {
    "Plot the distribution of nodes of a graph";
    this.updateState(
      step,
      name,
      slot,
      ["showGraphText", "showGraphFrame"],
      ["showNodeDistribution"]
    );
  }

  handlePlotCauses(step, name, slot) {
    "Plot the causes and effects per comment after CFE";
    this.updateState(step, name, slot, [], ["showCausesPlot"]);
  }

  handlePlotCausalMap(step, name, slot) {
    "Plot causal Map after CFE";
    this.updateState(step, name, slot, [], ["showCausalMap"]);
  }

  handleStatementText(step, name, slot) {
    "Plot Statement Graph";
    this.updateState(step, name, slot, [], ["showGraphText"]);
  }

  handleStatementFrame(step, name, slot) {
    "Plot Statement Graph";
    this.updateState(step, name, slot, [], ["showGraphFrame"]);
  }

  handleFetch(route, stateField, func, step, name, slot) {
    this.setState({ loading: true });
    const url = BASE_URL + route;
    let dataArray = this.state.datasets.map((d) => d.data);
    const fetches = dataArray.map((data) =>
      fetch(url, {
        method: "POST",
        headers: HEADERS,
        body: JSON.stringify(data),
      })
    );
    Promise.all(fetches)
      .then((responses) => Promise.all(responses.map((r) => r.json())))
      .then((jsonObjects) => {
        this.setState((prevState) => ({
          steps: [...prevState.steps.splice(0, slot - 1), step],
          prettySteps: [...prevState.prettySteps.splice(0, slot - 1), name],
          datasets: [
            ...prevState.datasets.map((dataset, i) => {
              dataset = this.removeAllKeysExcept(dataset, []);
              let temp = func(dataset, jsonObjects, stateField, i);
              return temp;
            }),
          ],
          loading: false,
        }));
      })
      .catch((error) => {
        this.handleError(error);
      });
  }

  examplePipeline() {
    this.setState({ steps: [], datasets: [], loading: true });
    fetch(BASE_URL + "example", {
      method: "GET",
      headers: HEADERS,
    })
      .then((data) => data.json())
      .then((data) => this.setState(data))
      .catch((error) => this.handleError(error));
  }

  render() {
    return (
      <>
        <Router basename={"/observatories/climate-change-opinion-observatory"}>
          <MyNavbar examplePipeline={this.examplePipeline} />
          <Switch>
            <Route path="/observatory">
              <Container fluid={true}>
                <Row>
                  <Col>
                    <h1 className="mt-4 mb-4">Pipeline</h1>
                  </Col>
                  <Col></Col>
                </Row>
                <Pipeline
                  steps={this.state.steps}
                  prettySteps={this.state.prettySteps}
                  isLoading={this.state.loading}
                  onFetch={this.handleFetch}
                  labels={this.state.datasets.map((x) => x.name)}
                  onAdd={this.handleAdd}
                  onWordCount={this.handleWordCounts}
                  onArticleCounts={this.handleArticleCounts}
                  onDelete={this.handleDelete}
                  onStatementText={this.handleStatementText}
                  onStatementFrame={this.handleStatementFrame}
                  onPlot={this.handlePlot}
                  onPlotCauses={this.handlePlotCauses}
                  onDistribution={this.handleDistribution}
                  onPlotCausalMap={this.handlePlotCausalMap}
                  onFinal={this.handleFinal}
                />
                <h1 className="mt-4 mb-4">Dashboard</h1>
                <Dashboard
                  isLoading={this.state.loading}
                  datasets={this.state.datasets}
                />
                {/*JSON.stringify(this.state)*/}
              </Container>
            </Route>
            <Route path="/about">
              <About />
            </Route>
            <Route path="/tutorial">
              <Tutorial examplePipeline={this.examplePipeline} />
            </Route>
            <Route path="/">
              <Home />
            </Route>
          </Switch>
        </Router>
      </>
    );
  }
}

export default App;
