import React from "react";
import { Card, CardBody, CardTitle, Row, Col } from "reactstrap";

import BootstrapTable from "react-bootstrap-table-next";
import paginationFactory from "react-bootstrap-table2-paginator";
import ToolkitProvider, {
  Search,
  CSVExport
} from "react-bootstrap-table2-toolkit";

const { ExportCSVButton } = CSVExport;
const { SearchBar } = Search;

//this.props.dataset must be a list of json objects
// [{},{},{},{}]

class Table extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      data: []
    };
  }

  render() {
    function process(cell) {
      let res = [];

      cell.forEach(c => {
        if (!c.entities) {
          res.push(c.utterance);
        } else {
          let utterance = c.utterance.split(" ");
          utterance = utterance.map(w => w + " ");
          c.entities.forEach((triple, tripleIndex) => {
            let causingWord = triple.cause2;
            let evokingWord = triple.frameEvokingElement2;
            let effectWord = triple.effect2;

            let causesMarked;
            let fev_marked;
            let effects_marked;

            if (causingWord) {
              causesMarked = causingWord.map(i => utterance[i - 1]);
              causesMarked = (
                <mark
                  key={tripleIndex + "cause"}
                  style={{ backgroundColor: "#9cc9cc", borderRadius: "0.25em" }}
                >
                  {causesMarked.map(w => w + " ")}
                  <span
                    style={{
                      fontSize: "6px",
                      backgroundColor: "white",
                      borderRadius: "0.25em"
                    }}
                  >
                    CAUSE
                  </span>
                </mark>
              );
            }

            if (evokingWord) {
              fev_marked = evokingWord.map(i => utterance[i - 1]);
              fev_marked = (
                <mark
                  key={tripleIndex + "evoke"}
                  style={{ backgroundColor: "#7aecec", borderRadius: "0.25em" }}
                >
                  {fev_marked.map(w => w + " ")}
                  <span
                    style={{
                      fontSize: "6px",
                      backgroundColor: "white",
                      borderRadius: "0.25em"
                    }}
                  >
                    FEV
                  </span>
                </mark>
              );
            }
            if (effectWord) {
              effects_marked = effectWord.map(i => utterance[i - 1]);
              effects_marked = (
                <mark
                  key={tripleIndex + "effect"}
                  style={{ backgroundColor: "#ff8197", borderRadius: "0.25em" }}
                >
                  {effects_marked.map(w => w + " ")}
                  <span
                    style={{
                      fontSize: "6px",
                      backgroundColor: "white",
                      borderRadius: "0.25em"
                    }}
                  >
                    EFFECT
                  </span>
                </mark>
              );
            }

            if (causesMarked) {
              let before = utterance.length;
              utterance.splice(
                causingWord[0] - 1,
                causingWord.length,
                ...[causesMarked]
              );
              if (before - utterance.length > 0) {
                utterance.splice(
                  causingWord[0],
                  0,
                  ...Array(causingWord.length - 1).fill(" ")
                );
              }
            }
            if (fev_marked) {
              let before = utterance.length;
              utterance.splice(
                evokingWord[0] - 1,
                evokingWord.length,
                ...[fev_marked]
              );
              if (before - utterance.length > 0) {
                utterance.splice(
                  evokingWord[0],
                  0,
                  ...Array(evokingWord.length - 1).fill(" ")
                );
              }
            }
            if (effectWord) {
              let before = utterance.length;
              utterance.splice(
                effectWord[0] - 1,
                effectWord.length,
                ...[effects_marked]
              );
              if (before - utterance.length > 0) {
                utterance.splice(
                  effectWord[0],
                  0,
                  ...Array(effectWord.length - 1).fill(" ")
                );
              }
            }
          });

          res.push(utterance);
        }
      });
      return res;
    }
    const columns = [
      {
        dataField: "date",
        text: "Article date",
        CSVExport: true,
        searchable: true
      },
      {
        dataField: "title",
        text: "Title",
        csvExport: true,
        searchable: true
      },
      {
        dataField: "text",
        text: "Text original",
        searchable: true,
        csvExport: true,
        hidden:
          this.props.dataset.data[0].framesets ||
          this.props.dataset.data[0].textmarked,
        formatter: (cell, row) => {
          return (
            <div
              style={{
                fontSize: 10,
                height: "150px",
                overflow: "auto",
                wordBreak: "break-all"
              }}
            >
              {cell}
            </div>
          );
        }
      },
      {
        dataField: "framesets",
        text: "Causal relations",
        searchable: false,
        csvExport: false,
        hidden: !this.props.dataset.data[0].framesets && true,
        formatter: (cell, row, rowIndex) => {
          return (
            <div
              style={{
                fontSize: 9,
                height: "150px",
                // width: "100px",
                overflow: "auto"
              }}
            >
              {process(cell)
              // JSON.stringify(cell)
              }
            </div>
          );
        }
      },
      {
        dataField: "textmarked",
        text: "Text marked",
        searchable: false,
        csvExport: false,
        hidden: !this.props.dataset.data[0].textmarked && true,
        formatter: (cell, row) => {
          return (
            <div
              style={{
                fontSize: 10,
                height: "150px",
                overflow: "auto",
                wordBreak: "break-all"
              }}
            >
              <div dangerouslySetInnerHTML={{ __html: cell }} />
            </div>
          );
        }
      }
      // {
      //   dataField: "framesets",
      //   text: "Causal relations",
      //   searchable: false,
      //   csvExport: false,
      //   hidden: !this.props.dataset.data[0].framesets && true,
      //   formatter: (cell, row) => {
      //     return (
      //       <div
      //         style={{
      //           fontSize: 9,
      //           height: "150px",
      //           width: "100px",
      //           overflow: "auto"
      //         }}
      //       >
      //         {JSON.stringify(cell)}
      //       </div>
      //     );
      //   }
      // }
    ];
    return (
      <div>
        <Card className="justify-content-between shadow w-100">
          <CardBody>
            <CardTitle className="text-center">
              <h3>{"Dataset"}</h3> {this.props.dataset.name.replace(/_/g, " ")}
            </CardTitle>
            <ToolkitProvider
              keyField="id"
              data={this.props.dataset.data}
              columns={columns}
              search
              exportCSV
            >
              {props => (
                <div>
                  <Row>
                    <Col>
                      <SearchBar {...props.searchProps} className="" />
                    </Col>
                    <Col className="text-right">
                      <ExportCSVButton
                        className="btn-danger"
                        {...props.csvProps}
                      >
                        Export CSV
                      </ExportCSVButton>
                    </Col>
                  </Row>
                  <BootstrapTable
                    {...props.baseProps}
                    rowStyle={{ fontSize: 8 }}
                    pagination={paginationFactory()}
                  />
                </div>
              )}
            </ToolkitProvider>
          </CardBody>
        </Card>
      </div>
    );
  }
}

export default Table;
