import React from "react";

import { Row, Col } from "reactstrap";

import DatasetLabel from "./datasetlabel.js";

class DatasetLabelBox extends React.Component {
  render() {
    return (
      <Col>
        {this.props.labels.map(el => (
          <Row key={el}>
            <DatasetLabel
              onDelete={this.props.onDelete}
              text={el}
            ></DatasetLabel>
          </Row>
        ))}
      </Col>
    );
  }
}

export default DatasetLabelBox;
