import React from "react";
import { ForceGraph2D } from "react-force-graph";
import { SizeMe } from "react-sizeme";
import { BASE_URL, HEADERS, filterIfDefined } from "./utilities.js";
import MainSpinner from "./spinner.js";
import BarChart from "./barchart.js";
import TextGraph from "./textgraph.js";
import {
  Card,
  CardBody,
  Col,
  Row,
  Form,
  Input,
  FormGroup,
  Label
} from "reactstrap";
import { render_displacy } from "./utilities.js";
import { DebounceInput } from "react-debounce-input";

class GraphRow extends React.Component {
  render() {
    if (!this.props.datasets) return null;
    return (
      <Row>
        {this.props.datasets.map(
          dataset =>
            dataset.showGraphText && (
              <Col sm="6" key={dataset.name}>
                <TextGraph
                  dataset={dataset}
                  title="Statement graph from sentences"
                  kind="text"
                />
              </Col>
            )
        )}
        {this.props.datasets.map(
          dataset =>
            dataset.showGraphFrame && (
              <Col sm="6" key={dataset.name}>
                <Graph
                  dataset={dataset}
                  title="Statement graph from causal relations"
                  kind="frames"
                />
              </Col>
            )
        )}
      </Row>
    );
  }
}

class Graph extends React.Component {
  constructor(props) {
    super(props);
    this.handleSelectChange = this.handleSelectChange.bind(this);
    this.state = {
      showField: "effect",
      keywordField: "cause",
      loading: false,
      inputValue: ""
    };
  }

  handleSelectChange({ target }) {
    this.setState(
      {
        showField: target.value,
        keywordField: target.value === "cause" ? "effect" : "cause"
      },
      this.fetchGraph
    );
  }

  fetchGraph() {
    this.setState({ loading: true });
    const url = BASE_URL + "get-statements";
    let type = this.state.showField;
    fetch(url, {
      method: "POST",
      headers: HEADERS,
      body: JSON.stringify({
        type: type,
        data: this.props.dataset.data
      })
    })
      .then(response => {
        return response.json();
      })
      .then(data => {
        this.setState({ graph: data, loading: false, origGraph: data });
      })
      .catch(error => {
        alert(error);
        this.setState({ loading: false });
      });
  }

  componentDidMount() {
    this.fetchGraph();
  }

  updateInput = evt => {
    this.setState({ inputValue: evt.target.value.toLowerCase() }, () => {
      let nodesToKeep = this.state.origGraph.nodes.map(node => node.id);
      if (this.state.inputValue !== "") {
        nodesToKeep = this.state.origGraph.nodes.filter(node =>
          filterIfDefined(this.state.keywordField, this.state.inputValue, node)
        );
        nodesToKeep = nodesToKeep.map(node => node.id);
      }
      this.setState({
        graph: {
          nodes: this.state.origGraph.nodes.filter(node =>
            nodesToKeep.includes(node.id)
          ),
          links: this.state.origGraph.links.filter(
            link =>
              nodesToKeep.includes(link.target.id) &&
              nodesToKeep.includes(link.source.id)
          )
        }
      });
    });
  };

  render() {
    function returnLabel(node) {
      let utterance = node.utterance.split(" ");
      utterance = utterance.map(w => w + " ");

      let causeIdcs = node.cause2;
      let effectIdcs = node.effect2;
      let fevIdcs = node.frameEvokingElement2;

      let causesMarked;
      let fev_marked;
      let effects_marked;

      if (causeIdcs) {
        causesMarked = causeIdcs.map(i => utterance[i - 1]);
        causesMarked =
          `<mark style="background-color:#9cc9cc; border-radius: 0.25em">` +
          causesMarked.join(" ") +
          `<span style="font-size:8px;background-color:white;border-radius:0.25em">CAUSE</span></mark>`;
      }
      if (fevIdcs) {
        fev_marked = fevIdcs.map(i => utterance[i - 1]);
        fev_marked =
          `<mark style="background-color:#7aecec; border-radius: 0.25em">` +
          fev_marked.join(" ") +
          `<span style="font-size:8px;background-color:white;border-radius:0.25em">FEV</span></mark>`;
      }
      if (effectIdcs) {
        effects_marked = effectIdcs.map(i => utterance[i - 1]);
        effects_marked =
          `<mark style="background-color:#ff8197; border-radius: 0.25em">` +
          effects_marked.join(" ") +
          `<span style="font-size:8px;background-color:white;border-radius:0.25em">EFFECT</span></mark>`;
      }
      if (causesMarked) {
        let before = utterance.length;
        utterance.splice(causeIdcs[0] - 1, causeIdcs.length, causesMarked);
        if (before - utterance.length > 0) {
          utterance.splice(
            causeIdcs[0],
            0,
            ...Array(causeIdcs.length - 1).fill(" ")
          );
        }
      }
      if (fev_marked) {
        let before = utterance.length;
        utterance.splice(fevIdcs[0] - 1, fevIdcs.length, fev_marked);
        if (before - utterance.length > 0) {
          utterance.splice(
            fevIdcs[0],
            0,
            ...Array(fevIdcs.length - 1).fill(" ")
          );
        }
      }
      if (effectIdcs) {
        let before = utterance.length;
        utterance.splice(effectIdcs[0] - 1, effectIdcs.length, effects_marked);
        if (before - utterance.length > 0) {
          utterance.splice(
            effectIdcs[0],
            0,
            ...Array(effectIdcs.length - 1).fill(" ")
          );
        }
      }
      return utterance.join(" ");
    }

    return (
      <>
        {this.props.dataset.showNodeDistribution && this.state.graph && (
          <BarChart
            loading={this.state.loading}
            onSelectChange={this.handleSelectChange}
            showField={this.state.showField}
            graph={this.state.graph}
            title="Frequencies of Causes/Effects"
          />
        )}
        <SizeMe monitorHeight>
          {({ size }) => (
            <Card className="shadow justify-content-between">
              <CardBody>
                <h3>{this.props.title}</h3>
                <Form inline>
                  <FormGroup>
                    <Label for="label" className="mr-2">
                      Show
                    </Label>
                    <Input
                      value={this.state.showField}
                      type="select"
                      name="showField"
                      id="showField"
                      onChange={this.handleSelectChange}
                      className="mr-2"
                    >
                      <option value="effect">effects</option>
                      <option value="cause">causes</option>
                    </Input>
                    <Label for="searchInput" className="mr-2">
                      for {this.state.keywordField}:
                    </Label>
                    <DebounceInput
                      id="searchInput"
                      className="form-control"
                      value={this.state.inputValue}
                      minLength={2}
                      debounceTimeout={900}
                      onChange={evt => this.updateInput(evt)}
                    />
                  </FormGroup>
                </Form>
                {this.state.loading ? (
                  <MainSpinner />
                ) : (
                  this.state.graph && (
                    <ForceGraph2D
                      graphData={this.state.graph}
                      nodeLabel={returnLabel}
                      nodeCanvasObjectMode={() => "after"}
                      nodeCanvasObject={(node, ctx, globalScale) => {
                        const label = node.mfic;
                        if (!label) return;
                        const fontSize = 12 / globalScale;
                        ctx.font = `${fontSize}px Sans-Serif`;
                        const textWidth = ctx.measureText(label).width;
                        const bckgDimensions = [textWidth, fontSize].map(
                          n => n + fontSize * 0.2
                        ); // some padding
                        ctx.fillStyle = "rgba(255, 255, 255, 0.8)";
                        ctx.fillRect(
                          node.x - bckgDimensions[0] / 2,
                          node.y - bckgDimensions[1] / 2,
                          ...bckgDimensions
                        );
                        ctx.textAlign = "center";
                        ctx.textBaseline = "middle";
                        ctx.fillStyle = "blue"; //node.color;
                        ctx.fillText(label, node.x, node.y);
                      }}
                      linkDirectionalArrowLength={5}
                      linkDirectionalArrowRelPos={1}
                      linkOpacity={0.5}
                      linkWidth={link => (link.weight ? link.weight : 1)}
                      width={size.width - 30}
                      height={600}
                    />
                  )
                )}
              </CardBody>
            </Card>
          )}
        </SizeMe>
      </>
    );
  }
}

export default GraphRow;
