import React from "react";
import WordTreeRow from "./wordtree.js";
import Table from "./table.js";
import { Row, Col } from "reactstrap";
import GraphRow from "./graph.js";
import JsonCard from "./jsoncard.js";
import LineRowCards from "./linechart.js";
import CauseEffectFrequenciesPlot from "./causeeffectfrequencies.js";
import MainSpinner from "./spinner.js";
import CausalMap from "./causalmap.js";

class Dashboard extends React.Component {
  render() {
    return (
      <div>
        {this.props.isLoading ? (
          <MainSpinner />
        ) : (
          <div>
            <CausalMap datasets={this.props.datasets} />
            <CauseEffectFrequenciesPlot datasets={this.props.datasets} />
            <LineRowCards datasets={this.props.datasets} />
            <JsonCard
              data={this.props.datasets}
              field={"entities"}
              title={"Entities Json Data"}
            />
            <JsonCard
              data={this.props.datasets}
              field={"articleCounts"}
              title={"Article Counts Json Data"}
            />
            <JsonCard
              data={this.props.datasets}
              field={"wordCounts"}
              title={"Article Counts Json Data"}
            />
            <GraphRow datasets={this.props.datasets} title={"Plot Graph"} />
            <WordTreeRow datasets={this.props.datasets} title={"Word tree"} />
          </div>
        )}
        <Row>
          {this.props.datasets.map((dataset) => (
            <Col sm="6" key={dataset.name}>
              <Table dataset={dataset}></Table>
            </Col>
          ))}
        </Row>
      </div>
    );
  }
}

export default Dashboard;
