import React from "react";
import {
  PopoverBody,
  UncontrolledPopover,
  Button,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter
} from "reactstrap";
class ResultSteps extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isModalOpen: false
    };
    this.toggle = this.toggle.bind(this);
  }

  publish() {
    this.toggle();
  }

  toggle() {
    this.setState({
      isModalOpen: !this.state.isModalOpen
    });
  }

  handleChange({ target }) {
    this.setState({
      [target.name]: target.value
    });
  }

  render() {
    return (
      <div>
        <Button
          id="finalButton"
          size="sm"
          disabled={this.props.disabled}
          className="mb-2"
          color="danger"
          onClick={() => {
            this.toggle();
            this.props.onFinish();
          }}
          block
        >
          Check pipeline steps
        </Button>
        <UncontrolledPopover
          trigger="hover"
          placement="right"
          target="finalButton"
        >
          <PopoverBody>Show the different pipeline steps</PopoverBody>
        </UncontrolledPopover>
        <Modal isOpen={this.state.isModalOpen} toggle={this.toggle}>
          <ModalHeader toggle={this.toggle}>
            Pipeline steps overview
          </ModalHeader>
          <ModalBody>
            <ol>
              {this.props.labels.map((label, i) => (
                <li key={label}>
                  Dataset {i}: {label}
                </li>
              ))}
              {this.props.pipelineSteps.map(step => (
                <li key={step}>{step}</li>
              ))}
            </ol>
          </ModalBody>
          <ModalFooter>
            <Button color="danger" onClick={this.toggle}>
              Close
            </Button>
          </ModalFooter>
        </Modal>
      </div>
    );
  }
}
export default ResultSteps;
