import React from "react";
import { Button, UncontrolledPopover, PopoverBody } from "reactstrap";

class CauseButtons extends React.Component {
  constructor(props) {
    super(props);
    this.handleStatement = this.handleStatement.bind(this);
    this.handlePlotCauses = this.handlePlotCauses.bind(this);
    this.handlePlotCausalMap = this.handlePlotCausalMap.bind(this);
    this.state = {};
  }
  handleStatement() {
    this.setState({ clicked: 1 });
    this.props.onStatement(
      "graph",
      "Create statement graph from extracted causes (https://penelope.vub.be/network-components/statementgraphgenerator)",
      this.props.slot
    );
  }
  handlePlotCauses() {
    this.setState({ clicked: 2 });
    this.props.onPlotCauses(
      "final",
      "Plot causes per utterance from causes (Visualisation)",
      this.props.slot
    );
  }
  handlePlotCausalMap() {
    this.setState({ clicked: 3 });
    this.props.onPlotCausalMap(
      "final",
      "Create causal map from extracted causes and effects (Visualisation)",
      this.props.slot
    );
  }
  render() {
    return (
      <div>
        <Button
          id="statementButton"
          block
          size="sm"
          color="danger"
          onClick={this.handleStatement}
          disabled={this.state.clicked === 1}
        >
          Statement graph
        </Button>
        <UncontrolledPopover
          trigger="hover"
          placement="right"
          target="statementButton"
        >
          <PopoverBody>
            Create a statement network graph from either the causes or the
            effects in the dataset. Two nodes ( = a node is an extracted cause
            of effect) are connected if they share a word lemma.{" "}
            <a href="/tutorial" target="_blank" rel="noopener noreferrer">
              More information
            </a>
          </PopoverBody>
        </UncontrolledPopover>
        <Button
          id="frameDistrButton"
          block
          size="sm"
          color="danger"
          onClick={this.handlePlotCauses}
          disabled={this.state.clicked === 2}
        >
          Plot Frame Distribution
        </Button>
        <UncontrolledPopover
          trigger="hover"
          placement="right"
          target="frameDistrButton"
        >
          <PopoverBody>
            Plot a barchart showing the frequency distribution of similar causes
            or similar effects.{" "}
            <a href="/tutorial" target="_blank" rel="noopener noreferrer">
              More information
            </a>
          </PopoverBody>
        </UncontrolledPopover>
        <Button
          id="causalMapButton"
          block
          size="sm"
          color="danger"
          onClick={this.handlePlotCausalMap}
          disabled={this.state.clicked === 3}
        >
          Plot Causal Map
        </Button>
        <UncontrolledPopover
          trigger="hover"
          placement="right"
          target="causalMapButton"
        >
          <PopoverBody>
            Plot a network graph connecting the causes and effects together.{" "}
            <a href="/tutorial" target="_blank" rel="noopener noreferrer">
              More information
            </a>
          </PopoverBody>
        </UncontrolledPopover>
      </div>
    );
  }
}

export default CauseButtons;
