import React from "react";
import ResultSteps from "./resultsteps.js";

class FinalButtons extends React.Component {
  constructor(props) {
    super(props);
    this.handleFinal = this.handleFinal.bind(this);
    this.state = {};
  }

  handleFinal() {
    this.props.onFinal("done", "Results", this.props.slot);
  }

  render() {
    return (
      <div>
        <ResultSteps
          onFinish={this.handleFinal}
          labels={this.props.labels}
          pipelineSteps={this.props.pipelineSteps}
        />
      </div>
    );
  }
}

export default FinalButtons;
