import React from "react";
import { Button } from "reactstrap";

class GraphButtons extends React.Component {
  constructor(props) {
    super(props);
    this.handleDistribution = this.handleDistribution.bind(this);
    this.state = {};
  }

  handleDistribution() {
    this.setState({ clicked: 1 });
    this.props.onDistribution(
      "final",
      "Create Barchart showing edge distribution from graph (Visualisation)",
      this.props.slot
    );
  }

  render() {
    return (
      <div>
        <Button
          block
          size="sm"
          color="danger"
          onClick={this.handleDistribution}
          disabled={this.state.clicked === 1}
        >
          Plot distribution
        </Button>
      </div>
    );
  }
}

export default GraphButtons;
