import React, { Component } from "react";
import { Col, Card, Row, CardBody, Label, FormGroup } from "reactstrap";

import HighchartsReact from "highcharts-react-official";
import Highcharts from "highcharts";
import HC_exporting from "highcharts/modules/exporting";

import { DebounceInput } from "react-debounce-input";

HC_exporting(Highcharts);

class LineRowCards extends Component {
  render() {
    return (
      <>
        {this.props.datasets.length > 0 &&
          this.props.datasets.map(
            (dataset) =>
              dataset.showEntitiesPlot && (
                <>
                  {dataset.entities && (
                    <LineChart
                      title="Named Entities frequencies over time"
                      showInitialData={false}
                      key={dataset.name}
                      data={dataset.entities.data}
                      categories={dataset.entities.categories}
                    />
                  )}
                  {dataset.articleCounts && (
                    <NormalLineChart
                      title="Article counts over time"
                      showInitialData={true}
                      key={dataset.name}
                      data={dataset.articleCounts.data}
                      categories={dataset.articleCounts.categories}
                    />
                  )}
                  {dataset.wordCounts && (
                    <LineChart
                      title="word counts over time"
                      showInitialData={false}
                      key={dataset.name}
                      data={dataset.wordCounts.data}
                      categories={dataset.wordCounts.categories}
                    />
                  )}
                </>
              )
          )}
      </>
    );
  }
}

class NormalLineChart extends Component {
  constructor(props) {
    super(props);
    this.state = {
      inputValue: "",
      chartOptions: {
        chart: { type: "area" },
        xAxis: {
          maxPadding: 0,
          type: "category",
          crosshair: true,
          categories: this.props.categories,
        },
        title: { text: this.props.title },
        plotOptions: {
          area: {
            stacking: "normal",
            lineColor: "#666666",
            lineWidth: 1,
            marker: { lineWidth: 1, lineColor: "#666666" },
          },
        },
        series: this.props.showInitialData ? this.props.data : [],
      },
    };
  }

  setHoverData = (e) => {
    this.setState({ hoverData: e.target.category });
  };

  render() {
    const { chartOptions } = this.state;
    return (
      <Row>
        <Col>
          <Card className="shadow justify-content-between">
            <CardBody>
              <HighchartsReact highcharts={Highcharts} options={chartOptions} />
            </CardBody>
          </Card>
        </Col>
      </Row>
    );
  }
}

class LineChart extends Component {
  constructor(props) {
    super(props);
    this.state = {
      inputValue: "",
      chartOptions: {
        chart: { type: "area" },
        xAxis: {
          maxPadding: 0,
          type: "category",
          crosshair: true,
          categories: this.props.categories,
        },
        title: { text: this.props.title },
        plotOptions: {
          area: {
            stacking: "normal",
            lineColor: "#666666",
            lineWidth: 1,
            marker: { lineWidth: 1, lineColor: "#666666" },
          },
        },
        series: this.props.showInitialData ? this.props.data : [],
      },
    };
  }

  setHoverData = (e) => {
    this.setState({ hoverData: e.target.category });
  };

  updateInput = (evt) => {
    if (!evt.target.value) return;
    this.setState({ inputValue: evt.target.value.toLowerCase() }, () => {
      let itemsToPlot = this.props.data.filter((data) =>
        data.name.toLowerCase().includes(this.state.inputValue)
      );
      this.setState((prevState) => ({
        ...prevState,
        chartOptions: {
          ...prevState.chartOptions,
          series: itemsToPlot,
        },
      }));
    });
  };

  render() {
    const { chartOptions } = this.state;
    return (
      <Row>
        <Col>
          <Card className="shadow justify-content-between">
            <CardBody>
              <Row form>
                <Col md={2}>
                  <FormGroup>
                    <Label for="searchInput">Search terms containing: </Label>
                    <DebounceInput
                      id="searchInput"
                      className="form-control"
                      value={this.state.inputValue}
                      minLength={3}
                      debounceTimeout={900}
                      onChange={(evt) => this.updateInput(evt)}
                    />
                  </FormGroup>
                </Col>
              </Row>
              <HighchartsReact highcharts={Highcharts} options={chartOptions} />
            </CardBody>
          </Card>
        </Col>
      </Row>
    );
  }
}

export default LineRowCards;
