export function countArticles(dataset) {
  let grouped = dataset.data.reduce((res, curr) => {
    res[curr.date] = res[curr.date] || [];
    res[curr.date].push(curr);
    return res;
  }, {});

  let dates = Object.keys(grouped);
  let listDate = createDateArray(_.min(dates), _.max(dates));
  let newGrouped = {};
  listDate.forEach((date) => {
    newGrouped[date] = grouped[date] || [];
  });

  //word counts here
  let articleCounts = _.map(newGrouped, (v, k) => v.length);

  dataset.articleCounts = {
    data: [{ name: "Article Counts", data: articleCounts }],
    categories: listDate,
  };

  return dataset;
}

export function countWords(dataset) {
  let grouped = dataset.data.reduce((res, curr) => {
    res[curr.date] = res[curr.date] || [];
    res[curr.date].push(curr);
    return res;
  }, {});

  let dates = Object.keys(grouped);
  let listDate = createDateArray(_.min(dates), _.max(dates));
  let newGrouped = {};

  console.log(listDate);
  listDate.forEach((date) => {
    newGrouped[date] = grouped[date] || [];
  });

  //word counts here

  let n = listDate.length;
  console.log(n);
  let newNewGrouped = {};

  let i = 0;
  _.forOwn(newGrouped, (listOfArticles, date) => {
    let current_date_all_text = "";
    listOfArticles.forEach((article) => {
      current_date_all_text = current_date_all_text + " " + article.text;
    });
    newGrouped[date] = current_date_all_text;

    //var words = "Hi there and hello there. Welcome and hello there.";

    var wordcnt = current_date_all_text
      .replace(/[^\w\s]/g, "")
      .split(/\s+/)
      .reduce(function (map, word) {
        map[word] = (map[word] || 0) + 1;
        return map;
      }, Object.create(null));

    console.log(wordcnt);
    _.forOwn(wordcnt, (number, word) => {
      if (word in newNewGrouped) {
        console.log("exists", word);
        newNewGrouped[word].splice(i, 1, number);
      } else {
        console.log("not exists", word);
        newNewGrouped[word] = new Array(n - 1).fill(0);
        newNewGrouped[word].splice(i, 0, number);
      }
    });
    i = i + 1;
    console.log(newNewGrouped);
  });

  //let articleCounts = _.map(newGrouped, (v, k) => v.length);

  let wordcountspairs = [];
  _.forOwn(newNewGrouped, (val, key) => {
    wordcountspairs.push({ name: key, data: val });
  });

  dataset.wordCounts = {
    data: wordcountspairs,
    categories: listDate,
  };

  return dataset;
}

export function createDateArray(startDate, endDate) {
  if (startDate === endDate) return [startDate];
  let listDate = [];
  let dateMove = new Date(startDate);
  let strDate = startDate;
  while (strDate < endDate) {
    strDate = dateMove.toISOString().slice(0, 10);
    listDate.push(strDate);
    dateMove.setDate(dateMove.getDate() + 1);
  }
  return listDate;
}

export function render_displacy(text, spans, ents, colorMap) {
  let result = `<p style="font-size:9px;">`;
  let offset = 0;
  spans.forEach(({ type, start, end }) => {
    const entity = text.slice(start, end);
    const fragments = text.slice(offset, start).split("\n");
    fragments.forEach((fragment, i) => {
      if (fragments.length > 1 && i !== fragments.length - 1) {
        result = result + fragment + "<br />";
      } else {
        result = result + fragment;
      }
    });
    offset = end;
    if (ents.includes(type.toLowerCase())) {
      result =
        result +
        `<mark style="background-color: ${colorMap[type.toLowerCase()]};
            border-radius: 0.25em; opacity: 0.66; padding: -1px;"> ${entity} ${type} </mark>`;
    } else {
      result = result + entity;
    }
  });

  result = result + text.slice(offset, text.length) + `</p>`;
  return result;
}

