export function convertLowerCaser(triple) {
  if ("cause" in triple && _.isString(triple.cause))
    triple.cause = triple.cause.toLowerCase();
  if ("effect" in triple && _.isString(triple.effect))
    triple.effect = triple.effect.toLowerCase();
  if ("fev" in triple && _.isString(triple.fev))
    triple.fev = triple.fev.toLowerCase();
  return triple;
}

export function filterIfDefined(record, filter, e) {
  if (filter) {
    if (record in e && e[record].includes(filter)) {
      return true;
    } else {
      return false;
    }
  } else {
    return true;
  }
}

export function createTableName(
  source,
  comments,
  subreddit,
  term,
  start,
  stop,
  number
) {
  return (
    source +
    "_" +
    (comments ? "comments_" : "") +
    (source === "reddit" ? subreddit + "_" : "") +
    term +
    "_" +
    start +
    "_" +
    stop +
    "_" +
    number
  );
}

export function count(array) {
  return array.reduce((prev, nxt) => {
    prev[nxt] = prev[nxt] + 1 || 1;
    return prev;
  }, {});
}

export function json2tuples(json) {
  return Object.keys(json).map((key, i) => {
    return [key, json[key]];
  });
}

export function sortCounts(counts) {
  return counts.sort(function (first, second) {
    return second[1] - first[1];
  });
}

export default function getURL(
  source,
  term,
  start,
  stop,
  number,
  comments,
  subreddit
) {
  const path = {
    guardian: "test?",
    parlgbr: "get-parl-data-gbr?",
    parlger: "get-parl-data-ger?",
    twitter: "twitter-data?",
    reddit: "get-reddit-data?",
  };
  let params = new URLSearchParams({
    term: term,
    start: start,
    stop: stop,
    number: number,
    comments: comments,
    subreddit: subreddit,
  });
  return BASE_URL + path[source] + params.toString();
}

