import React from "react";
import { Button, UncontrolledPopover, PopoverBody } from "reactstrap";

class TextButtons extends React.Component {
  constructor(props) {
    super(props);
    this.handleCause = this.handleCause.bind(this);
    this.handleEntities = this.handleEntities.bind(this);
    this.handleWordTree = this.handleWordTree.bind(this);
    this.handleStatement = this.handleStatement.bind(this);

    this.handleArticleCounts = this.handleArticleCounts.bind(this);
    this.handleWordCounts = this.handleWordCounts.bind(this);
    this.state = {};
  }
  handleStatement() {
    this.setState({ clicked: 1 });
    this.props.onStatement(
      "graph",
      "Create statement graph from extracted causes (https://penelope.vub.be/network-components/statementgraphgenerator)",
      this.props.slot
    );
  }
  handleEntities() {
    const func = (dataset, json, stateField, i) => {
      dataset.entities = json[i].counts;
      dataset.data = json[i].text;
      return dataset;
    };
    this.setState({ clicked: 2 });
    this.props.onFetch(
      "get-entities",
      "entities",
      func,
      "numerical",
      "Get Named Entities from text (https://penelope.vub.be/spacy-api/texts-named-entities)",
      this.props.slot
    );
  }
  handleCause() {
    if (
      window.confirm(
        `This operation can take a while, are you 
        sure you want to continue with a dataset of this size?`
      )
    ) {
      const func = (dataset, json, stateField, i) => {
        dataset.data = json[i].articles;
        return dataset;
      };
      this.setState({ clicked: 3 });
      this.props.onFetch(
        "get-cause",
        null,
        func,
        "list",
        "Get Causal Frames per article from text (https://penelope.vub.be/semantic-frame-extractor/texts-extract-frames)",
        this.props.slot
      );
    } else {
      // Do nothing!
    }
  }
  handleWordTree() {
    const func = (dataset, json, stateField, i) => {
      dataset[stateField] = json[i];
      return dataset;
    };
    this.setState({ clicked: 4 });
    this.props.onFetch(
      "get-word-tree",
      "tree",
      func,
      "final",
      "Get Word Tree from text (Visualisation)",
      this.props.slot
    );
  }
  handleArticleCounts() {
    this.setState({ clicked: 6 });
    this.props.onArticleCounts(
      "numerical",
      "Count the word frequencies",
      this.props.slot
    );
  }
  handleWordCounts() {
    this.setState({ clicked: 5 });
    this.props.onWordCount(
      "numerical",
      "Count the word frequencies",
      this.props.slot
    );
  }
  render() {
    return (
      <div>
        <Button
          id="statementButton"
          //type="button"
          block
          size="sm"
          color="danger"
          onClick={this.handleStatement}
          text={"Statement Graph"}
          disabled={this.state.clicked === 1}
        >
          Statement Graph
        </Button>
        <UncontrolledPopover
          trigger="hover"
          placement="right"
          target="statementButton"
        >
          <PopoverBody>
            Create a statement network graph from all the sentences in the
            dataset. Two nodes(=sentences) are connected if they share a word
            lemma.{" "}
            <a href="/tutorial" target="_blank" rel="noopener noreferrer">
              More information
            </a>
          </PopoverBody>
        </UncontrolledPopover>
        <Button
          id="entitiesButton"
          block
          size="sm"
          color="danger"
          onClick={this.handleEntities}
          disabled={this.state.clicked === 2}
        >
          Count Named Entities
        </Button>
        <UncontrolledPopover
          trigger="hover"
          placement="right"
          target="entitiesButton"
        >
          <PopoverBody>
            Extract the Named Entities (geographic locations, persons,
            organisations, ...) from a text with machine learning methods.{" "}
            <a href="/tutorial" target="_blank" rel="noopener noreferrer">
              More information
            </a>
          </PopoverBody>
        </UncontrolledPopover>
        <Button
          id="causeButton"
          block
          size="sm"
          color="danger"
          onClick={this.handleCause}
          disabled={this.state.clicked === 3}
        >
          Causal Frames
        </Button>
        <UncontrolledPopover
          trigger="hover"
          placement="right"
          target="causeButton"
        >
          <PopoverBody>
            Extract the cause-and-effect relations from text{" "}
            <a href="/tutorial" target="_blank" rel="noopener noreferrer">
              More information
            </a>
          </PopoverBody>
        </UncontrolledPopover>
        <Button
          id="treeButton"
          block
          size="sm"
          color="danger"
          onClick={this.handleWordTree}
          disabled={this.state.clicked === 4}
        >
          Word tree
        </Button>
        <UncontrolledPopover
          trigger="hover"
          placement="right"
          target="treeButton"
        >
          <PopoverBody>
            Create a word tree from the text{" "}
            <a href="/tutorial" target="_blank" rel="noopener noreferrer">
              More information
            </a>
          </PopoverBody>
        </UncontrolledPopover>
        <Button
          id="wordCountButton"
          block
          size="sm"
          color="danger"
          disabled={this.state.clicked === 5}
          onClick={this.handleWordCounts}
        >
          Term frequency
        </Button>
        <UncontrolledPopover
          trigger="hover"
          placement="right"
          target="wordCountButton"
        >
          <PopoverBody>
            Show term frequency over time{" "}
            <a href="/tutorial" target="_blank" rel="noopener noreferrer">
              More information
            </a>
          </PopoverBody>
        </UncontrolledPopover>
        <Button
          id="articleCountButton"
          block
          size="sm"
          color="danger"
          disabled={this.state.clicked === 6}
          onClick={this.handleArticleCounts}
        >
          Article Counts
        </Button>
        <UncontrolledPopover
          trigger="hover"
          placement="right"
          target="articleCountButton"
        >
          <PopoverBody>
            Show the article counts{" "}
            <a href="/tutorial" target="_blank" rel="noopener noreferrer">
              More information
            </a>
          </PopoverBody>
        </UncontrolledPopover>
      </div>
    );
  }
}

export default TextButtons;
