import React from "react";

import { Badge } from "reactstrap";

class DataLabel extends React.Component {
  constructor(props) {
    super(props);
    this.handleDelete = this.handleDelete.bind(this);
  }

  handleDelete() {
    this.props.onDelete(this.props.text);
  }

  render() {
    return (
      <Badge
        color="danger"
        className="h1 btn-block"
        style={{ whiteSpace: "normal" }}
      >
        {this.props.text.replace(/_/g, " ")}
        <span
          aria-hidden="true"
          onClick={this.handleDelete}
          style={{ color: "black", fontSize: "24px", cursor: "pointer" }}
        >
          &times;
        </span>
      </Badge>
    );
  }
}

export default DataLabel;
