import React, { Fragment } from "react";
import { Col, Row, Card, CardBody, CardTitle } from "reactstrap";
import PipelineCard from "./pipelinecard.js";
import StepProgressBar from "./progressbar.js";
import DatasetLabelBox from "./datasetLabelBox.js";
import Modal from "./modal.js";
import TextButtons from "./textbuttons.js";
import GraphButtons from "./graphbuttons.js";
import NumericalButtons from "./numericalbuttons.js";
import CauseButtons from "./causebuttons.js";
import FinalButtons from "./finalbuttons.js";

class Pipeline extends React.Component {
  render() {
    const buttons = (name, slot) => {
      if (this.props.labels.length < 1) return;
      if (name === "text")
        return (
          <TextButtons
            slot={slot}
            onFetch={this.props.onFetch}
            onStatement={this.props.onStatementText}
            onArticleCounts={this.props.onArticleCounts}
            onWordCount={this.props.onWordCount}
          />
        );
      if (name === "graph")
        return (
          <GraphButtons
            slot={slot}
            onDistribution={this.props.onDistribution}
          />
        );
      if (name === "numerical")
        return <NumericalButtons slot={slot} onPlot={this.props.onPlot} />;

      if (name === "list")
        return (
          <CauseButtons
            slot={slot}
            onFetch={this.props.onFetch}
            onStatement={this.props.onStatementFrame}
            onPlotCauses={this.props.onPlotCauses}
            onPlotCausalMap={this.props.onPlotCausalMap}
          />
        );
      if (name === "final")
        return (
          <FinalButtons
            slot={slot}
            labels={this.props.labels}
            pipelineSteps={this.props.prettySteps}
            onFinal={this.props.onFinal}
          />
        );
    };

    return (
      <Row>
        <Col>
          <Card
            className="shadow"
            style={{ minHeight: "240px", maxHeight: "320px" }}
          >
            <CardBody>
              <CardTitle> Choose dataset(s)</CardTitle>
              <Modal
                disabled={this.props.labels.length >= 2}
                buttonLabel="Add Dataset"
                onAdd={(src, trm, t1, t2, nr, c, sr) => {
                  this.props.onAdd(src, trm, t1, t2, nr, c, sr);
                }}
              />
              <DatasetLabelBox
                onDelete={(evt) => {
                  this.props.onDelete(evt);
                }}
                labels={this.props.labels}
              ></DatasetLabelBox>
            </CardBody>
          </Card>
        </Col>

        {[...Array(4)].map((e, i) => (
          <Fragment key={i}>
            <Col lg="1" xl="1" className="d-none d-lg-block">
              <StepProgressBar
                percent={this.props.steps.length >= i + 1 && 100}
              ></StepProgressBar>
            </Col>
            <Col>
              <PipelineCard
                title={"Step " + (i + 1)}
                body={buttons(
                  i === 0 ? "text" : this.props.steps[i - 1],
                  i + 1
                )}
              ></PipelineCard>
            </Col>
          </Fragment>
        ))}

        {/* <Col lg="1" xl="1" className="d-none d-lg-block">
          <StepProgressBar
            percent={this.props.steps.length >= 1 && 100}
          ></StepProgressBar>
        </Col>
        <Col>
          <PipelineCard
            title={"Step 1"}
            body={buttons("text", 1)}
          ></PipelineCard>
        </Col>

        <Col lg="1" xl="1" className="d-none d-lg-block">
          <StepProgressBar
            percent={this.props.steps.length >= 2 && 100}
          ></StepProgressBar>
        </Col>
        <Col>
          <PipelineCard
            title={"Step 2"}
            body={buttons(this.props.steps[0], 2)}
          ></PipelineCard>
        </Col>

        <Col lg="1" xl="1" className="d-none d-lg-block">
          <StepProgressBar
            percent={this.props.steps.length >= 3 && 100}
          ></StepProgressBar>
        </Col>
        <Col>
          <PipelineCard
            title={"Step 3"}
            body={buttons(this.props.steps[1], 3)}
          ></PipelineCard>
        </Col>

        <Col lg="1" xl="1" className="d-none d-lg-block">
          <StepProgressBar
            percent={this.props.steps.length >= 4 && 100}
          ></StepProgressBar>
        </Col>
        <Col>
          <PipelineCard
            title={"Step 4"}
            body={buttons(this.props.steps[2], 4)}
          ></PipelineCard>
        </Col> */}
      </Row>
    );
  }
}

export default Pipeline;
