import React from "react";
import { ForceGraph2D } from "react-force-graph";
import { SizeMe } from "react-sizeme";
import { filterIfDefined, convertLowerCaser } from "./utilities.js";
import {
  Card,
  CardBody,
  Col,
  Row,
  CardTitle,
  FormGroup,
  Label,
} from "reactstrap";
import { DebounceInput } from "react-debounce-input";

class GraphRow extends React.Component {
  render() {
    if (!this.props.datasets) return null;
    return (
      <Row>
        {this.props.datasets.map(
          (dataset) =>
            dataset.showCausalMap && (
              <Col sm="6" key={dataset.name}>
                <Graph dataset={dataset} />
              </Col>
            )
        )}
      </Row>
    );
  }
}

class Graph extends React.Component {
  constructor(props) {
    super(props);
    this.getGraph = this.getGraph.bind(this);
    this.updateGraph = this.updateGraph.bind(this);
    this.state = {
      inputValue: "",
      graph: this.getGraph("", "cause"),
      label: "cause",
    };
  }

  getGraph(keyword) {
    if (!this.props.dataset.data.length > 0) return { nodes: [], links: [] };
    let triples = this.props.dataset.data
      .map((d) => d.framesets)
      .map((lst) => lst.filter((e) => e.entities))
      .map((lst) => lst.map((e) => e.entities))
      .map((lst) => [...new Set(lst.flat())]) //count once per comment!
      .filter((lst) => lst.length)
      .flat()
      .filter(
        (e) =>
          filterIfDefined("cause", keyword, e) ||
          filterIfDefined("effect", keyword, e)
      )
      .map((e) => convertLowerCaser(e));
    let nodes = [];
    let links = [];
    const seen_causes = new Set();
    const seen_effects = new Set();

    triples.forEach((triple, i) => {
      if (triple.cause) {
        if (seen_effects.has(triple.cause)) {
          //The cause is also an effect
          let idx = nodes.findIndex((node) => node.id === triple.cause);
          nodes[idx].group = 3;
          nodes[idx].val = nodes[idx].val + 1;
        } else {
          if (!seen_causes.has(triple.cause)) {
            seen_causes.add(triple.cause);
            nodes.push({
              id: triple.cause,
              label: triple.cause,
              group: 1,
              val: 1,
            });
          }
        }
      }
      if (triple.effect) {
        if (seen_causes.has(triple.effect)) {
          //The effect is also a cause
          let idx = nodes.findIndex((node) => node.id === triple.effect);
          nodes[idx].group = 3;
          nodes[idx].val = nodes[idx].val + 1;
        } else {
          if (!seen_effects.has(triple.effect)) {
            seen_effects.add(triple.effect);
            nodes.push({
              id: triple.effect,
              label: triple.effect,
              group: 2,
              val: 1,
            });
          }
        }
      }
      if (triple.cause && triple.effect)
        //TODO check if exists if so increase weight by one
        links.push({
          weight: 1,
          source: triple.cause,
          target: triple.effect,
          label: "causes", //triple.frameEvokingElement,
          color: "red",
        });
    });
    return { nodes: nodes, links: links };
  }

  updateGraph() {
    this.setState({
      graph: this.getGraph(this.state.inputValue),
    });
  }

  updateInput = (evt) => {
    this.setState({ inputValue: evt.target.value.toLowerCase() }, () => {
      this.updateGraph();
    });
  };

  render() {
    return (
      <SizeMe monitorHeight>
        {({ size }) => (
          <Card className="shadow justify-content-between">
            <CardBody>
              <Row>
                <Col sm="6">
                  <CardTitle className="text-center">
                    <h3>{"Causal Map"}</h3>
                  </CardTitle>
                </Col>
                <Col sm="6">
                  <FormGroup>
                    <Label for="searchInput">Search </Label>
                    <DebounceInput
                      id="searchInput"
                      className="form-control"
                      value={this.state.inputValue}
                      minLength={1}
                      debounceTimeout={900}
                      onChange={(evt) => this.updateInput(evt)}
                    />
                  </FormGroup>
                </Col>
              </Row>
              <ForceGraph2D
                graphData={this.state.graph}
                nodeAutoColorBy="group"
                nodeLabel={(node) => node.label}
                width={size.width - 30}
                height={600}
                linkDirectionalArrowLength={5}
                linkDirectionalArrowRelPos={1}
                linkCanvasObjectMode={() => "after"}
                linkCanvasObject={(link, ctx, globalScale) => {
                  const label = link.label;
                  ctx.fillStyle = "red";
                  ctx.fillText(
                    label,
                    (link.target.x + link.source.x) / 2,
                    (link.target.y + link.source.y) / 2
                  );
                }}
                nodeCanvasObject={(node, ctx, globalScale) => {
                  const label = node.id;
                  if (!label) return;
                  const fontSize = 11 / globalScale;
                  ctx.font = `${fontSize}px Sans-Serif`;
                  const textWidth = ctx.measureText(label).width;
                  const bckgDimensions = [textWidth, fontSize].map(
                    (n) => n + fontSize * 0.2
                  ); // some padding
                  ctx.fillStyle = "rgba(255, 255, 255, 0.8)";
                  ctx.fillRect(
                    node.x - bckgDimensions[0] / 2,
                    node.y - bckgDimensions[1] / 2,
                    ...bckgDimensions
                  );
                  ctx.textAlign = "center";
                  ctx.textBaseline = "middle";
                  ctx.fillStyle = node.color;
                  ctx.fillText(label, node.x, node.y);
                }}
              />
            </CardBody>
            <p> {JSON.stringify(this.state.graph)}</p>
          </Card>
        )}
      </SizeMe>
    );
  }
}

export default GraphRow;
