import React, { Component } from "react";
import {
  Col,
  Card,
  Row,
  CardBody,
  Label,
  FormGroup,
  Form,
  Input
} from "reactstrap";
import { json2tuples, sortCounts, filterIfDefined } from "./utilities.js";
import HighchartsReact from "highcharts-react-official";
import Highcharts from "highcharts";
import HC_exporting from "highcharts/modules/exporting";
import { DebounceInput } from "react-debounce-input";
import stringSimilarity from "string-similarity";
HC_exporting(Highcharts);

//After cause sentences

class BarRowCards extends Component {
  render() {
    return (
      <Row>
        {this.props.datasets.length > 0 &&
          this.props.datasets.map(
            dataset =>
              dataset.showCausesPlot && (
                <Col sm="6" key={dataset.name}>
                  <BarChart dataset={dataset} />
                </Col>
              )
          )}
      </Row>
    );
  }
}

class BarChart extends Component {
  constructor(props) {
    super(props);
    this.handleSelectChange = this.handleSelectChange.bind(this);
    this.state = {
      showField: "effect",
      keywordField: "cause",
      inputValue: "",
      chartOptions: {
        chart: {
          type: "column"
        },
        title: {
          text: "Show effects for cause keyword: *"
        },
        xAxis: {
          type: "category",
          labels: {
            rotation: -55,
            style: {
              fontSize: "9px",
              fontFamily: "Verdana, sans-serif"
            }
          }
        },
        yAxis: {
          allowDecimals: false,
          min: 0,
          title: {
            text: "Number of occurences"
          }
        },
        legend: {
          enabled: false
        },
        series: [
          {
            name: "Frequency",
            data: this.count(this.props.dataset.data, "", "cause", "effect")
          }
        ]
      }
    };
  }

  count(data, keyword, keywordField, showField) {
    let commentFrames = data
      .map(d => d.framesets)
      .map(lst =>
        lst
          .filter(e => e.entities)
          .map(e => e.entities)
          .flat()
          .filter(e => filterIfDefined(keywordField, keyword, e))
          .map(e => e[showField])
      )
      .map(lst => [...new Set(lst)])
      .filter(lst => lst.length)
      .flat()
      .filter(e => typeof e === "string" && e !== "")
      .map(e => e.toLowerCase());

    //https://www.npmjs.com/package/string-similarity#api

    function scan(res, sentence) {
      let found = {};
      Object.keys(res).forEach((k, i) => {
        let sim = stringSimilarity.compareTwoStrings(k, sentence);
        if (sim > 0.6) {
          found[k] = sim;
        }
      });
      //return best match
      if (Object.entries(found).length === 0 && found.constructor === Object)
        return false;
      return Object.keys(found).reduce((a, b) => (found[a] > found[b] ? a : b));
    }

    let res = {};
    commentFrames.forEach(sentence => {
      if (sentence in res) {
        //add restuple + 1
        console.log("SAME", sentence, " - ", sentence);
        res[sentence] = res[sentence] + 1;
      } else {
        let found = scan(res, sentence);
        if (found) {
          //add restuple + 1
          console.log("SIMILAR", sentence, " - ", found);
          res[found] = res[found] + 1;
        } else {
          //add new restuple with sentence = 1
          console.log("NEW", " - ", sentence);
          res[sentence] = 1;
        }
      }
    });

    res = json2tuples(res);
    res = sortCounts(res);
    return res;
  }

  setHoverData = e => {
    this.setState({ hoverData: e.target.category });
  };

  updateChart() {
    let itemsToPlot = this.count(
      this.props.dataset.data,
      this.state.inputValue,
      this.state.keywordField,
      this.state.showField
    );
    this.setState(prevState => ({
      ...prevState,
      chartOptions: {
        ...prevState.chartOptions,
        series: [
          {
            name: "Frequency",
            data: itemsToPlot
          }
        ],
        title: {
          text:
            "Show " +
            this.state.showField +
            "s for " +
            this.state.keywordField +
            " keyword: " +
            (this.state.inputValue === "" ? "*" : this.state.inputValue)
        }
      }
    }));
  }

  updateInput = evt => {
    this.setState({ inputValue: evt.target.value.toLowerCase() }, () =>
      this.updateChart()
    );
  };

  handleSelectChange({ target }) {
    this.setState(
      {
        showField: target.value,
        keywordField: target.value === "cause" ? "effect" : "cause"
      },
      () => {
        this.updateChart();
      }
    );
  }

  render() {
    const { chartOptions } = this.state;
    return (
      <Card className="shadow justify-content-between">
        <CardBody>
          <Form inline>
            <FormGroup>
              <Label for="label" className="mr-2">
                Show
              </Label>
              <Input
                value={this.state.showField}
                type="select"
                name="showField"
                id="showField"
                onChange={this.handleSelectChange}
                className="mr-2"
              >
                <option value="effect">effects</option>
                <option value="cause">causes</option>
              </Input>
              <Label for="searchInput" className="mr-2">
                for {this.state.keywordField}:
              </Label>
              <DebounceInput
                id="searchInput"
                className="form-control"
                value={this.state.inputValue}
                minLength={2}
                debounceTimeout={900}
                onChange={evt => this.updateInput(evt)}
              />
            </FormGroup>
          </Form>
          <HighchartsReact highcharts={Highcharts} options={chartOptions} />
        </CardBody>
      </Card>
    );
  }
}

export default BarRowCards;
