import React from "react";
import HighchartsReact from "highcharts-react-official";
import Highcharts from "highcharts";
import HC_exporting from "highcharts/modules/exporting";
import MainSpinner from "./spinner.js";
import {
  sortCounts,
  count,
  json2tuples,
  filterIfDefined
} from "./utilities.js";
import { Card, CardBody, Form, FormGroup, Label } from "reactstrap";
import { DebounceInput } from "react-debounce-input";

HC_exporting(Highcharts);

class BarChart extends React.Component {
  constructor(props) {
    super(props);
    this.countNodes = this.countNodes.bind(this);
    this.state = {
      inputValue: "",
      chartOptions: {
        chart: {
          type: "column"
        },
        title: {
          text: "Show effects for cause: all"
        },
        xAxis: {
          type: "category",
          labels: {
            rotation: -55,
            style: {
              fontSize: "9px",
              fontFamily: "Verdana, sans-serif"
            }
          }
        },
        yAxis: {
          min: 0,
          title: {
            text: "Number of occurences"
          }
        },
        legend: {
          enabled: false
        },
        series: [
          {
            name: "Frequency",
            data: this.countNodes(this.props.graph, "")
          }
        ]
      }
    };
  }

  componentWillReceiveProps(nextProps) {
    this.setState({
      inputValue: "",
      chartOptions: {
        chart: {
          type: "column"
        },
        title: {
          text: "Show effects for cause: all"
        },
        xAxis: {
          type: "category",
          labels: {
            rotation: -55,
            style: {
              fontSize: "9px",
              fontFamily: "Verdana, sans-serif"
            }
          }
        },
        yAxis: {
          min: 0,
          title: {
            text: "Number of occurences"
          }
        },
        legend: {
          enabled: false
        },
        series: [
          {
            name: "Frequency",
            data: this.countNodes(nextProps.graph, "")
          }
        ]
      }
    });
  }

  countNodes(graph, keyword) {
    let words = graph.nodes;
    if (words.length === 0) return [];
    words = words.filter(node => filterIfDefined("text", keyword, node));
    words = words.map(node => node.mfic);
    words = words.filter(word => word !== "");
    words = count(words);
    words = json2tuples(words);
    words = sortCounts(words);
    return words;
  }

  setHoverData = e => {
    this.setState({ hoverData: e.target.category });
  };

  updateInput = evt => {
    this.setState({ inputValue: evt.target.value.toLowerCase() }, () => {
      let itemsToPlot = this.countNodes(
        this.props.graph,
        this.state.inputValue
      );
      this.setState(prevState => ({
        ...prevState,
        chartOptions: {
          ...prevState.chartOptions,
          series: [
            {
              name: "Frequency",
              data: itemsToPlot
            }
          ],
          title: {
            text:
              "Show effects for cause: " +
              (this.state.inputValue === "" ? "all" : this.state.inputValue)
          }
        }
      }));
    });
  };

  render() {
    const { chartOptions } = this.state;
    return (
      <Card className="shadow justify-content-between">
        <CardBody>
          <h3>{this.props.title}</h3>
          <Form inline>
            <FormGroup>
              <Label for="label" className="mr-2">
                Search in sentences:
              </Label>
              <DebounceInput
                id="searchInput"
                className="form-control"
                value={this.state.inputValue}
                minLength={2}
                debounceTimeout={900}
                onChange={evt => this.updateInput(evt)}
              />
            </FormGroup>
          </Form>
          {this.props.loading ? (
            <MainSpinner />
          ) : (
            <HighchartsReact highcharts={Highcharts} options={chartOptions} />
          )}
        </CardBody>
      </Card>
    );
  }
}

export default BarChart;
